<template>
	<page-meta :page-style="'overflow:'+(showDate?'hidden':'visible')"></page-meta>
	<view class="index">
		<!-- <img class="bgIcon" src="../../static/bg_icon.png" alt=""> -->
		<view class="noPhone" v-if="!phone">
			<view class="space20"></view>
			<button class="getPhone" type="primary" open-type="getPhoneNumber" @getphonenumber="onGetPhoneNumber">
				手机号登录
			</button>
		</view>

		<template v-else>
			<view @click="showDate = true" class="date">
				{{$u.timeFormat(searchDate, 'yyyy年mm月dd日')}}
				<img class="dateIcon" src="../../static/date_icon.png" alt="">
			</view>

			<view class="title">收入</view>
			<view class="card">
				<template v-for="(item,ind) in incomeList">
					<view class="titleData2">
						{{item.title}}
						<view v-for="rate in item.rateList" class="data">{{rate.name}} <text
								:class="[rate.rate>0?'upColor':'downColor']">{{Number(rate.rate)}}%</text>
						</view>
					</view>
					<view class="barChtItem">
						<qiun-data-charts type="bar" :canvas2d="true" :canvasId="'incomeList'+ind" :opts="optsCustomList"
							:chartData="item.histogramData" />
					</view>
				</template>
			</view>

			<view class="title">客户</view>
			<view class="card">
				<template v-for="(item,ind) in customList">
					<view class="titleData2">
						{{item.title}}
						<view v-for="rate in item.rateList" class="data">{{rate.name}} <text
								:class="[rate.rate>0?'upColor':'downColor']">{{Number(rate.rate)}}%</text>
						</view>
					</view>
					<!-- min: item.histogramData.series[1]<0?item.histogramData.series[1]+10:0, -->

					<view class="barChtItem">
						<qiun-data-charts type="bar" :canvas2d="true" :canvasId="'customList'+ind" :opts="optsIncomeList"
							:chartData="item.histogramData" />
					</view>
				</template>
			</view>
		</template>



		<u-datetime-picker closeOnClickOverlay @close="showDate=false" @cancel="showDate=false" @confirm="showDate = false"
			:show="showDate" v-model="searchDate" mode="date"></u-datetime-picker>


	</view>
</template>

<script>
	import wybTable from '@/components/wyb-table/wyb-table.vue'
	import {
		getMenu,
		loginApi,
		infoApi,
		phoneApi,
		miniInfoApi,
		briefingWeekApi
	} from '../../config/api.js'
	import {
		openid
	} from '../../config/config.js'
	export default {
		components: {
			wybTable
		},
		data() {
			/*
			 const today = new Date()
			 const yesterday = new Date(today)

			 yesterday.setDate(yesterday.getDate() - 1)

			 today.toDateString()
			 yesterday.toDateString()
			 */
			return {
				loginData: {},
				phone: uni.getStorageSync('phone') || null,
				searchDate: Number(new Date()),
				showDate: false,

				//客户
				optsIncomeList: {
					color: ["#1890FF", "#91CB74", "#FAC858", "#EE6666", "#73C0DE", "#3CA272", "#FC8452", "#9A60B4", "#ea7ccc"],
					padding: [0, 70, 0, 0],
					// dataLabel: false,
					enableScroll: false,
					legend: {},
					xAxis: {
						min: 0,
						boundaryGap: "justify",
						disableGrid: true,
						disabled: true,
						axisLine: false,
					},
					yAxis: {
						disabled: true,
						disableGrid: true,
					},
					extra: {
						bar: {
							type: "group",
							width: 10,
							meterBorde: 1,
							meterFillColor: "#FFFFFF",
							activeBgColor: "#000000",
							activeBgOpacity: 0.08,
							linearType: "custom",
							barBorderCircle: true,
							seriesGap: 8,
							categoryGap: 2
						}
					}
				},
				// 收入
				optsCustomList: {
					color: ['#2c58a7', '#6f93ce', '#87abca'],
					padding: [0, 70, 0, 0],
					// dataLabel: false,
					enableScroll: false,
					legend: {},
					xAxis: {
						min: 0,
						boundaryGap: "justify",
						disableGrid: true,
						disabled: true,
						axisLine: false,
					},
					yAxis: {
						disabled: true,
						disableGrid: true,
					},
					extra: {
						bar: {
							type: "group",
							width: 10,
							meterBorde: 1,
							meterFillColor: "#FFFFFF",
							activeBgColor: "#000000",
							activeBgOpacity: 0.08,
							linearType: "none",
							barBorderCircle: true,
							seriesGap: 8,
							categoryGap: 2
						}
					}
				},
				// 客户
				customList: [],
				// 收入
				incomeList: [],
			};
		},
		onReady() {
			if (!this.phone) {
				this.toLogin();
			}
		},
		onShow() {
			// this.toLogin();
			if (this.phone) {
				this.briefingDay(this.searchDate)
			}
		},
		watch: {
			searchDate(newValue, oldValue) {
				this.briefingDay(newValue)
			},
		},
		methods: {
			// 获取日报表数据
			async briefingDay(searchDate) {
				let date = uni.$u.timeFormat(searchDate, 'yyyy-mm-dd');
				const res = await briefingWeekApi({
					date
				})
				// const res = {
				// 	obj: {
				// 		customList: [{
				// 			rateList: [{
				// 				name: '订购量1',
				// 				rate: 0.25
				// 			}, {
				// 				name: '订购量2',
				// 				rate: -0.25
				// 			}],
				// 			histogramData: {
				// 				categories: [''],
				// 				series: [{
				// 					name: '日新增1',
				// 					data: [30]
				// 				}, {
				// 					name: '日新增2',
				// 					data: [-8]
				// 				}, ]
				// 			}
				// 		}, ],
				// 		incomeList: [{
				// 				rateList: [{
				// 					name: '订购量',
				// 					rate: 0.25
				// 				}],
				// 				histogramData: {
				// 					categories: [''],
				// 					series: [{
				// 						name: '日新增1',
				// 						data: [30]
				// 					}, {
				// 						name: '日新增2',
				// 						data: [-8]
				// 					}, {
				// 						name: '日新增3',
				// 						data: [20]
				// 					}, ]
				// 				}
				// 			},
				// 			{
				// 				rateList: [{
				// 					name: '订购量',
				// 					rate: 0.25
				// 				}],
				// 				histogramData: {
				// 					categories: [''],
				// 					series: [{
				// 						name: '日新增1',
				// 						data: [30]
				// 					}, {
				// 						name: '日新增2',
				// 						data: [-8]
				// 					}, {
				// 						name: '日新增3',
				// 						data: [20]
				// 					}, ]
				// 				}
				// 			},
				// 			{
				// 				rateList: [{
				// 					name: '订购量',
				// 					rate: 0.25
				// 				}],
				// 				histogramData: {
				// 					categories: [''],
				// 					series: [{
				// 						name: '日新增1',
				// 						data: [30]
				// 					}, {
				// 						name: '日新增2',
				// 						data: [-8]
				// 					}, {
				// 						name: '日新增3',
				// 						data: [20]
				// 					}, ]
				// 				}
				// 			},
				// 		],
				// 	}
				// }
				// 月到达
				this.incomeList = res.obj.incomeList;
				// 产品
				this.customList = res.obj.customList;
			},
			// 登录
			async toLogin() {
				const res = await new Promise((resolve, reject) => {
					uni.login({
						provider: 'weixin',
						success: async (res) => {
							console.log('微信登录成功', res);
							resolve(res)
						},
					});
				})
				const data = await loginApi({
					code: res.code,
				})
				this.loginData = data
				console.log('loginApi：', data);
				if (data.phone && data.auth) {
					this.briefingDay(this.searchDate)
					this.phone = data.phone
					await uni.setStorageSync('phone', data.phone);
				} else if (!data.auth) {
					uni.showModal({
						title: '提示',
						content: '手机号不在白名单，请联系管理员添加~',
						showCancel: false
					});
					this.phone = null
					await uni.setStorageSync('phone', '');
				}
				await uni.setStorageSync('openid', data.openid);
			},
			// 获取手机号
			async onGetPhoneNumber(result) {
				if (result.detail.errMsg !== "getPhoneNumber:ok") {
					return;
				}
				console.log('onGetPhoneNumber', result.detail);
				const openid = await uni.getStorageSync('openid')
				// 手机号接口
				const data = await phoneApi({
					encryptedData: result.detail.encryptedData,
					iv: result.detail.iv,
					openid: openid,
				})
				console.log('phoneApi：', data);

				if (data.phone && data.auth) {
					this.briefingDay(this.searchDate)
					this.phone = data.phone
					await uni.setStorageSync('phone', data.phone);
				} else if (!data.auth) {
					uni.showModal({
						title: '提示',
						content: '手机号不在白名单，请联系管理员添加~',
						showCancel: false
					});
					this.phone = null
					await uni.setStorageSync('phone', '');
				}
				await uni.setStorageSync('token', data.token);
			},
		}
	};
</script>

<style lang="scss" scoped>
	/* 请根据实际需求修改父元素尺寸，组件自动识别宽高 */
	page {}

	/* 	.index ::v-deep .u-popup {
		position: relative !important;
		z-index: 100000000 !important;
	} */

	.index {
		width: 100%;
		// background: url('~@/static/bg.png') 100% 100% no-repeat;
		/* 加载背景图 */
		// background-image: url('~@/static/bg.png'), url('~@/static/bg_icon.png');
		background-image: url('~@/static/bg_icon.png'), linear-gradient(-45deg, #eceeef, #daeeff);
		// background-color: linear-gradient(-45deg, #eceeef, #daeeff);
		background-position: center -110rpx;
		/* 背景图不平铺 */
		background-repeat: no-repeat;
		/* 当内容高度大于图片高度时，背景图像的位置相对于viewport固定 */
		// background-attachment: fixed;
		/* 让背景图基于容器大小伸缩 */
		background-size: 100%;
		position: relative;
		padding: 24rpx;
		box-sizing: border-box;

		.noPhone {
			height: 100vh;
			display: flex;
			flex-direction: column;

			.getPhone {
				margin-top: 150px;
				width: 70%;
			}
		}

		.space20 {
			margin-bottom: 20px;
		}

		.downColor {
			color: #E83C3C;
		}

		.upColor {
			color: #5BBB06;
		}

		.bgIcon {
			width: 174px;
			height: 208px;
			position: absolute;
			left: 50%;
			top: -50px;
			transform: translateX(-50%);
			z-index: 0;
		}

		.date {
			font-family: Source Han Sans CN;
			font-weight: 400;
			font-size: 10px;
			color: #000000;
			display: flex;
			align-items: flex-end;
			justify-content: flex-end;
			margin-bottom: 12px;

			.dateIcon {
				width: 17px;
				height: 17px;
				margin-left: 6px;
			}
		}

		.title {
			font-family: Source Han Sans CN;
			font-weight: 400;
			font-size: 18px;
			color: #0C0C0C;
			margin-bottom: 13px;
			margin-left: 16px;
		}

		.card {
			padding: 19px 12px;
			background: #F7FDFE;
			border-radius: 23px;
			margin-bottom: 29px;
			z-index: 1;

			&.noPadding {
				padding: 19px 0;

				.titleData2 {
					margin: 0 12px 19px;
				}
			}

			&.white {
				background: white;
			}

			.titleData {
				font-family: Source Han Sans CN;
				font-weight: 400;
				font-size: 12px;
				color: #333333;
				display: flex;
				align-items: center;
				justify-content: flex-end;
				margin: 13px 20px;

				&.spaceBetween {
					justify-content: space-between;
					margin: 13px 0px;

					.down {
						top: 5px !important;
					}

					.up {
						top: -2px !important;
					}
				}

				.data {
					margin: 0 4px;
					font-weight: 500;

					&.down {
						color: #E83C3C;
					}

					&.up {
						color: #5BBB06;
					}

				}

				.direction {
					width: 0;
					height: 0;
					border-width: 7px;
					/* 设置三角形的大小 */
					border-style: solid;
					display: inline-block;
					position: relative;

					&.down {
						top: 3px;
						border-color: #E83C3C transparent transparent transparent;
						/* 控制上下左右四条边颜色为红色 */
					}

					&.up {
						top: -3px;
						border-color: transparent transparent #5BBB06 transparent;
						/* 控制上下左右四条边颜色为红色 */
					}
				}
			}

			.titleData2 {
				display: flex;
				font-family: Source Han Sans CN;
				font-weight: bold;
				font-size: 15px;
				color: #0C0C0C;
				align-items: flex-end;
				margin-bottom: 19px;
				flex-wrap: wrap;

				&.spaceBetween {
					justify-content: space-between;

					.data {
						font-size: 14px;
						color: #444;
					}
				}

				.data {
					font-family: Source Han Sans CN;
					font-weight: 400;
					font-size: 12px;
					color: #333333;
					margin-left: 8px;

					text {
						margin-left: 3px;
					}
				}
			}

			.barChtItem {
				margin-top: -20px;
				height: 90px;
			}

			.space26 {
				margin-bottom: 26px;
			}
		}
	}

	.charts-box {
		display: flex;
		width: 100%;
		margin: 0 auto;
		justify-content: space-around;
		align-items: center;
		margin-bottom: 25px;

		.arcbarCht {
			width: 30%;
			height: 100px;
		}

		.barCht {
			width: 64%;
			height: 100px;
		}
	}
</style>